}

.d7 {
  background: #535fc7;
  margin-bottom: 20px;
  padding: 10px 0;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .3);
}

.d7:after {
  content: "";
  clear: both;
  display: table;
}

.d7 form {
  width: auto;
  float: right;
  margin-right: 30px;
}

.d7 input {
  width: 250px;
  height: 42px;
  background: #fff;
  border: 2px solid #3d324e;
  border-radius: 42px;
