  display: flex;
}

.photo-card {
  width: 23%;
  flex-direction: column;
  justify-content: space-around;
  padding: 10px;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .3);
}

.info {
  padding: 10px 0;
